/**
 * 
 * @prama t 
 * @return str MM-dd HH:mm
 */
export function formatDate(t) {
  t = t || Date.now();
  let time = new Date(t);
  let str = time.getMonth() < 9 ? '0' + (time.getMonth() + 1) : time.getMonth() + 1;
  str += '-';
  str += time.getDate() < 10 ? '0' + time.getDate() : time.getDate();
  str += ' ';
  str += time.getHours();
  str += ':';
  str += time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes();
  return str;
};

/**
 * 
 * @prama time 13
 * @return str x / x / x
 */
export function formateTime(time) {
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;
  const now = new Date().getTime();
  const diffValue = now - time;

  // 
  const secondC = diffValue / second;
  const minC = diffValue / minute;
  const hourC = diffValue / hour;
  const dayC = diffValue / day;

  if (dayC >= 1) {
    return parseInt(dayC) + "";
  } else if (hourC >= 1) {
    return parseInt(hourC) + "";
  } else if (minC >= 1) {
    return parseInt(minC) + "";
  } else if (secondC >= 1) {
    return parseInt(secondC) + "";
  } else {
    return '0';
  }
}

export function StringToTime(time) {
  const dateTimeStr = time; // 
  const date = new Date(dateTimeStr); //  Date 
  const year = date.getFullYear(); // 
  const month = date.getMonth() + 1; // 10
  const day = date.getDate(); // 
  const hour = date.getHours(); // 
  const minute = date.getMinutes(); // 
  const second = date.getSeconds(); // 

  const formattedDateTime =
    `${year}-${month.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')} ${hour.toString().padStart(2, '0')}:${minute.toString().padStart(2, '0')}:${second.toString().padStart(2, '0')}`;
  console.log(formattedDateTime); // 2023-04-02 03:33:31
  return formattedDateTime
}
